.Home__container{
    display: flex;
    flex-flow: column;
    flex: 1;
    /* background: radial-gradient(circle at bottom left, #ffffff, #fea01c 10%); */
    background-image: url('../../images/fondo_dibujos.jpg');
    /* background-repeat: no-repeat; */
    /* background-size: cover; */
}
.Menu__title{
    font-size: 2rem;
    width: 100%;
    height: fit-content;
    background: #fff;
    box-shadow: 0 0 .2em #fea01c;
    z-index: 1;
    display: flex;
    align-items: baseline;
    padding: .3em;
}
.Menu__title h1{
    font-size: 2rem;
}
.Menu__title span{
    color: #fea01c;
    font-size: 3rem;
}
.Home-wrapper{
    width: 100%;
    display: flex;
    background: radial-gradient(circle farthest-side at left, rgba(255, 255, 255, 0.185), rgb(255, 255, 255) 70%);
    flex: 1;
}
.Home__container__img{
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column;
}
.Home__container__img img{
    width: 50%;
    border-radius: 50%;
}
.Home__container__text{
    position: relative;
    width: 50%;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
    overflow: hidden;
    text-align: center;
}

.Home__container__text__blog-entries{
    display: flex;
    width: 60%;
    height: 30%;
    justify-content: space-between;
}
.Home__text__blog-entries{
    width: 60%;
    font-size: 1.5rem;
}
.Home__text__blog-entries p{
    font-weight: 600;
}
.Home__blog-entries{
    margin-top: .5em;
    width: 40%;
    background: white;
    border: 1px solid #fea01c;
}
.Home__blog-entries img{
    width: 100%;
    height: 100%;
}

@media (max-width: 1100px) {
    .Home__container__img img{
        width: 75%;
    }
    .Home__text__blog-entries, .Home__container__text__blog-entries{
        width: 80%;
    }
    .Home__blog-entries{
        width: 12em;
        height: 12em;
    }
}
@media (max-width: 650px){
    .Home-wrapper{
        flex-flow: column;
        align-items: center;
        padding: .7em 0;
    }
    .Home__container__img, .Home__container__text{
        width: 90%;
    }
}