@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&family=Indie+Flower&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Indie Flower', cursive;
  scroll-behavior: smooth;
}
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root{
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
          flex-flow: column;
}

.Menu-nav__container{
   display: -webkit-flex;
   display: flex;
}

/* Position and sizing of burger button */
.bm-burger-button {
    position: fixed;
    width: 36px;
    height: 30px;
    right: 2em;
    top: 2em;
  }
  
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    background: #383838;
  }
  
  /* Color/shape of burger icon bars on hover*/
  .bm-burger-bars-hover {
    background: #fea01c;
  }
  
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    height: 24px;
    width: 24px;
  }
  
  /* Color/shape of close button cross */
  .bm-cross {
    background: #bdc3c7;
  }
  
  /*
  Sidebar wrapper styles
  Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
  */
  .bm-menu-wrap {
    position: fixed;
    height: 100%;
  }
  
  /* General sidebar styles */
  .bm-menu {
    background: #fea01c;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
  }
  
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #373a47;
  }
  
  /* Wrapper for item list */
  .bm-item-list {
    color: #b8b7ad;
    padding: 0.8em;
  }
  
  /* Individual item */
  .bm-item {
    display: inline-block;
  }
  
  /* Styling of overlay */
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3);
  }
.Footer__container{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 0.2em;
    box-shadow: 0 0 .2em #fea01c;
    z-index: 1;
    padding: 0.4em;
}

.Footer__social-media{
    display: -webkit-flex;
    display: flex;
    width: 20%;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
}

.Footer__container a{
    font-size: 1.8rem;
    color: rgb(27, 27, 27);
    display: -webkit-flex;
    display: flex;
}
.Home__container{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    -webkit-flex: 1 1;
            flex: 1 1;
    /* background: radial-gradient(circle at bottom left, #ffffff, #fea01c 10%); */
    background-image: url(/sandra-blog/static/media/fondo_dibujos.9f344fc9.jpg);
    /* background-repeat: no-repeat; */
    /* background-size: cover; */
}
.Menu__title{
    font-size: 2rem;
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background: #fff;
    box-shadow: 0 0 .2em #fea01c;
    z-index: 1;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: baseline;
            align-items: baseline;
    padding: .3em;
}
.Menu__title h1{
    font-size: 2rem;
}
.Menu__title span{
    color: #fea01c;
    font-size: 3rem;
}
.Home-wrapper{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    background: radial-gradient(circle farthest-side at left, rgba(255, 255, 255, 0.185), rgb(255, 255, 255) 70%);
    -webkit-flex: 1 1;
            flex: 1 1;
}
.Home__container__img{
    width: 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-flow: column;
            flex-flow: column;
}
.Home__container__img img{
    width: 50%;
    border-radius: 50%;
}
.Home__container__text{
    position: relative;
    width: 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    overflow: hidden;
    text-align: center;
}

.Home__container__text__blog-entries{
    display: -webkit-flex;
    display: flex;
    width: 60%;
    height: 30%;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.Home__text__blog-entries{
    width: 60%;
    font-size: 1.5rem;
}
.Home__text__blog-entries p{
    font-weight: 600;
}
.Home__blog-entries{
    margin-top: .5em;
    width: 40%;
    background: white;
    border: 1px solid #fea01c;
}
.Home__blog-entries img{
    width: 100%;
    height: 100%;
}

@media (max-width: 1100px) {
    .Home__container__img img{
        width: 75%;
    }
    .Home__text__blog-entries, .Home__container__text__blog-entries{
        width: 80%;
    }
    .Home__blog-entries{
        width: 12em;
        height: 12em;
    }
}
@media (max-width: 650px){
    .Home-wrapper{
        -webkit-flex-flow: column;
                flex-flow: column;
        -webkit-align-items: center;
                align-items: center;
        padding: .7em 0;
    }
    .Home__container__img, .Home__container__text{
        width: 90%;
    }
}
.SoulBlog__container{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-flex-flow: column;
            flex-flow: column;
    color:#fff;
    font-size: 2em;
    background: #000;
}

.SoulBlog__header{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    background: linear-gradient(to bottom left,#0d50af63, #000 30%);
}

.SoulBlog__header__text{
    width: 50%;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}
.SoulBlog__header__link{
    font-size: 1em;
    margin-top: 2em;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
    padding: 2px 15px;
    cursor: pointer;
    font-family: 'Architects Daughter', cursive;
    border-radius: 10px;
    background: #0d50af; /* #19b4ff*/
    border: 1px solid #0c428f;
    color: rgb(241, 236, 236);
