}
.SoulBlog__header__link:hover{
    background: #42c0ff;
}
.SoulBlog__header__text p{
    width: 80%;
}
.SoulBlog__header__img{
    height: 100%;
    width: 50%;
}
.SoulBlog__header__img img{
    height: 100vh;
    width: 100%;
}

.SoulBlog__header__text img{
    width: 50%;
}


.SoulBlog__content{
    padding-top: 4em;
    min-height: 100vh;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    position: relative;
    background: linear-gradient(to top, #0d50af7c, rgba(0,0,0,.5) );
}
.Blog__paragraph{
    width: 70%;
}
.Blog__paragraph p{
    margin-bottom: 1em;
}


.reveal {
    position: relative;
    -webkit-transform: translateY(30px);
            transform: translateY(30px);
    opacity: 0;
    transition: all 2s ease;
}

.reveal.active {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
    opacity: 1;
}


@media (max-width:950px) {
    .SoulBlog__container{
        font-size: 1.4rem;
    }
    .SoulBlog__header{
        -webkit-flex-direction: column;
                flex-direction: column;
        position: relative;
    }
    .SoulBlog__header > *{
        width: 100%;
    }
    .SoulBlog__header__link{
        margin-bottom: 1em;
    }
    .SoulBlog__header__text{
        color: rgb(255, 255, 255);
        position: absolute;
        top: 50px;
        margin: auto;
        background: rgba(0, 0, 0, 0.466);
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
        width: 90%;
        border-radius: 20px;
        -webkit-backdrop-filter: blur(6px);
                backdrop-filter: blur(6px);
        border: 1px solid rgba(95, 95, 95, 0.274);
        box-shadow: 0 0 15px 10px rgba(83, 83, 83, 0.26);
    }
    .SoulBlog__header__text img{
        width: 60%;
    }
    .Blog__paragraph{
        width: 90%;
    }
}
